import Base from 'modules/base'
import { select, selectAll } from 'lib/selectors'
import { findGetParameter } from 'lib/helpers'
import { Manager, AFTER_PUSH_STATE, AFTER_REPLACE_STATE } from 'lib/events'
import flatpickr from "flatpickr"

const MODULE_SELECTOR = '#admin-calendar'
const el = select(MODULE_SELECTOR)

class NullClass {
  constructor() {
  }
}

class Admin extends (el ? Base : NullClass) {
    /**
     * @param HTMLCollectionOject el
     */
    constructor(el) {
    super(el ? el : null)
    if (el && this.isStaged()) {
      this.selectors = {
        CALENDAR_SELECTOR: '#admin-calendar-container',
        SERVER_DATE_SELECTOR: '#admin-calendar-server-date',
        SERVER_TIMEZONE_SELECTOR: '#admin-calendar-server-timezone',
        DISPLAY_LOCAL_TIMEZONE_SELECTOR: '.js-admin-calendar-local-timezone',
        DISPLAY_SERVER_TIMEZONE_SELECTOR: '.js-admin-calendar-server-timezone',
        IS_PLURIAL_SELECTOR: '.js-admin-calendar-is-plurial',
        DISPLAY_SERVER_DIFF_SELECTOR: '.js-admin-calendar-server-diff',
        ONLINE_LINK_SELECTOR: '.js-admin-calendar-online-link',
        GOTO_SELECTOR: '.js-admin-calendar-goto',
        RESET_SELECTOR: '.js-admin-calendar-reset',
        DISPLAY_ICON_SELECTOR: '.display-icon'
      }
            this.isDisplayed = false
            this.defaultDate = null
            this.displayCalendar = this.displayCalendar.bind(this)
            this.goTo = this.goTo.bind(this)
            this.onChangeState = this.onChangeState.bind(this)
            this.selectdDate = null
            this.init()
        }
    }

    /**
     * @returns {boolean}
     */
    isStaged() {
    const isStaged = !!(location.host.match(/[-]staged[-:]/g))
        this.el.classList.toggle('is-hidden', !isStaged)

        return isStaged
    }

    /**
     * @returns {void}
     */
    init() {
    this.calendar = select(this.selectors.CALENDAR_SELECTOR) || false
        if (!this.calendar) {
      return
        }

        const getParameterDate = findGetParameter('previewdate')
        const localTimezone = this.getLocalTimezone()
    let serverTimezone = select(this.selectors.SERVER_TIMEZONE_SELECTOR) || false
    let serverDate = select(this.selectors.SERVER_DATE_SELECTOR) || false

        serverTimezone = (serverTimezone) ? parseInt(serverTimezone.value.split(':')) : false
        serverDate = (serverDate) ? serverDate.value : false

    this.defaultDate = (/false/i).test(getParameterDate) ? this.formatToday() : ((this.defaultDate = getParameterDate || this.formatDate(serverDate, serverTimezone, true))) ? this.defaultDate : this.formatToday()

        this.render()
        this.afterRender(serverDate, localTimezone, serverTimezone)
        this.bind()
        this.setOnlineViewLink()

        if (!getParameterDate && serverDate) {
            this.historyPush()
        }
    }

    /**
     * @returns {void}
     */
    historyPush() {
        const parameters = this.formatParameters()
    history.replaceState({date: this.defaultDate}, 'Preview page', `?previewdate=${this.defaultDate}${parameters}`)
    }

    /**
     * @returns {string}
     */
    formatParameters() {
        let result = ''
        let tmp = []

        location.search.substr(1).split('&').forEach(item => {
            tmp = item.split('=')
            if (tmp && tmp[0] && (tmp[0] !== 'previewdate') && tmp[1]) {
                result += `&${tmp[0]}=${tmp[1]}`
            }
        })

        return result
    }

    /**
     * @param int localTimezone
     * @param int serverTimezone
     * @returns {void}
     */
    afterRender(serverDate, localTimezone, serverTimezone) {
    const diffElem = select(this.selectors.DISPLAY_SERVER_DIFF_SELECTOR) || false
    const plurialElem = selectAll(this.selectors.IS_PLURIAL_SELECTOR) || false
    const resetElem = select(this.selectors.RESET_SELECTOR) || false
    const localTimezoneElem = select(this.selectors.DISPLAY_LOCAL_TIMEZONE_SELECTOR) || false
    const serverTimezoneElem = select(this.selectors.DISPLAY_SERVER_TIMEZONE_SELECTOR) || false
        const calendarDiv = select('.flatpickr-calendar', this.el) || false

        if (calendarDiv) {
            calendarDiv.classList.add('showTimeInput')
            calendarDiv.classList.add('no-border')
        }

        localTimezoneElem.innerHTML = (localTimezoneElem && localTimezone != 0)
            ? ((localTimezone < 0) ? `${localTimezone}` : `+${localTimezone}`) : 0

        serverTimezoneElem.innerHTML = (serverTimezoneElem && serverTimezone != 0)
            ? ((serverTimezone < 0) ? `${serverTimezone}` : `+${serverTimezone}`) : 0

        const timeDifference = serverTimezone - localTimezone
        if (diffElem) {
          diffElem.innerHTML = timeDifference
        }
        if (plurialElem) {
      plurialElem.forEach(elem => {
        elem.classList.toggle('is-hidden', (timeDifference != 0))
      })
    }

    if (resetElem) {
      resetElem.classList.toggle('is-hidden', !serverDate)
    }
  }

  /**
   * @returns {void}
   */
  render() {
    this.instance = flatpickr(this.calendar, {
      enableTime: true,
      dateFormat: 'Y-m-dTH:i',
      inline: true,
      time_24hr: true,
      defaultDate: this.defaultDate,
      onReady: () => {
        this.targetDatesByIndex()
        this.targetInputHour()
        this.targetInputMinute()
        this.targetCurrentMonth()
        this.targetCurrentYear()
      },
      onChange: () => {
        this.targetDatesByIndex()
    }
    })
  }

  targetDatesByIndex() {
    selectAll('.dayContainer span').forEach( (date, index) => {
      date.setAttribute('data-test', `lblDatesBy_${index}`)
    })
  }

  targetInputHour() {
    select('.flatpickr-hour').setAttribute('data-test', 'txtInputHour')
  }

  targetInputMinute() {
    select('.flatpickr-minute').setAttribute('data-test', 'txtInputMinute')
  }

  targetCurrentMonth() {
    select('.cur-year').parentNode.setAttribute('data-test', 'lblCurrentMonthCalendarTray')
  }

  targetCurrentYear() {
    select('.flatpickr-current-month > div').setAttribute('data-test', 'lblCurrentYearCalendarTray')
  }

    /**
     * @returns {number}
     */
    getLocalTimezone() {
        let localTimezone = new Date().getTimezoneOffset()

        const localOffsetSign = (localTimezone < 0) ? '+' : '-'
        localTimezone = parseInt(Math.abs(localTimezone / 60))
        localTimezone = parseInt(localOffsetSign + localTimezone)

        return localTimezone
    }

    /**
     * FORMAT: Y-m-dTH:i
     *
     * @param bool date
     * @param string sourceTimezone
     * @param boolean isUtc
     * @param string targetTimezone
     * @returns {booleanÂ | string}
     */
    formatDate(date, sourceTimezone, isUtc, targetTimezone) {
    if (!date || !sourceTimezone) {
            return  false
        }
        isUtc = isUtc || false
        targetTimezone = targetTimezone || this.getLocalTimezone()

        const targetDate = new Date(date)
        sourceTimezone = (isNaN(sourceTimezone)) ? 0 : sourceTimezone
        targetTimezone = (isNaN(targetTimezone)) ? 0 : targetTimezone

        if (isUtc) {
            targetDate.setUTCHours(targetDate.getUTCHours() + sourceTimezone)
            const hours = (targetDate.getUTCHours() > 9) ? targetDate.getUTCHours() : `0${targetDate.getUTCHours()}`
            const minutes = (targetDate.getUTCMinutes() > 9) ? targetDate.getUTCMinutes() : `0${targetDate.getUTCMinutes()}`
            const month = targetDate.getUTCMonth() + 1
            const formattedMonth = (month > 9) ? month: `0${month}`
            const utcDate = targetDate.getUTCDate()
            const formattedDate = (targetDate.getUTCDate() > 9) ? utcDate: `0${utcDate}`
            return  `${targetDate.getUTCFullYear()}-${formattedMonth}-${formattedDate}T${hours}:${minutes}`
        }

        targetDate.setHours(targetDate.getHours() + sourceTimezone)
        const hours = (targetDate.getHours() > 9) ? targetDate.getHours() : `0${targetDate.getHours()}`
        const minutes = (targetDate.getMinutes() > 9) ? targetDate.getMinutes() : `0${targetDate.getMinutes()}`
        const month = targetDate.getMonth() + 1
        const formattedMonth = (month > 9) ? month: `0${month}`
        const date1 = targetDate.getDate()
        const formattedDate = (targetDate.getDate() > 9) ? date1: `0${date1}`
        return  `${targetDate.getFullYear()}-${formattedMonth}-${formattedDate}T${hours}:${minutes}`
    }

    /**
     * @returns {void}
     */
    bind() {
    if ((this.icon = select(this.selectors.DISPLAY_ICON_SELECTOR) || false)) {
            this.icon.addEventListener('click', this.displayCalendar)
        }

    if ((this.btn = select(this.selectors.GOTO_SELECTOR) || false)) {
            this.btn.addEventListener('click', this.goTo)
        }

        Manager.subscribe(AFTER_PUSH_STATE, this.onChangeState)
        Manager.subscribe(AFTER_REPLACE_STATE, this.onChangeState)
    }

    onChangeState(channel, event) {
        this.stateChanged(event.namespace)
    }

    stateChanged(event) {
        this.historyPush()
    }

    /**
     * @returns {void}
     */
    setOnlineViewLink() {
        const origin = (location.host.match(/[-]staged[:]/g))
            ? location.origin.replace(/[-]staged[:]/g, ':')
            : location.origin.replace(/[-]staged[-]/g, '-')

    if ((this.link = select(this.selectors.ONLINE_LINK_SELECTOR) || false)) {
            this.link.setAttribute('href', `${origin}${location.pathname}`)
        }
    }

    /**
     * @returns {void}
     */
    displayCalendar() {
    this.isDisplayed = (this.isDisplayed) ? false : true
        el.classList.toggle('hidden', (this.isDisplayed === false))
        el.classList.toggle('showed', (this.isDisplayed === true))
    }

    /**
     * @returns {void}
     */
    goTo() {
        this.selectedDate = this.calendar.value || false
        this.displayCalendar()

        const env = (location.host.match(/local/g)) ? 'site=chanel&' : ''
        const link = `${location.origin}${location.pathname}?${env}previewdate=${this.selectedDate}`

        location.assign(link)
    }

    /**
     * @returns {string}
     */
    formatToday() {
    const today = new Date()
        return `${today.getFullYear()}-${today.getMonth() + 1}-${today.getDate()}T00:01`
    }
}

export default new Admin(el)
